html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 100vw;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  position: relative;
  overflow-x: hidden;
  background: linear-gradient(to bottom, transparent, transparent 80%, #5ccfd3 80%, #5ccfd3 100%);
}
body a {
  text-decoration: none;
}

.hidden {
  display: none !important;
}

.show {
  display: block !important;
}

.w-100 {
  width: 100% !important;
}

.bg-white {
  background: #ffffff !important;
}

.text-lightBlue {
  color: #5ccfd3 !important;
}

.no-scroll {
  overflow: hidden !important;
}

.title-description {
  max-width: 500px;
  margin: 0 2.5rem;
  font-size: 14px;
  font-weight: 500;
}
@media (min-width: 48em) {
  .title-description {
    font-size: 16px;
  }
}
@media (min-width: 71.875em) {
  .title-description {
    margin: 0 5rem;
    font-size: 28px;
  }
}

.title-header {
  max-width: 500px;
  margin: 0 2.5rem;
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 48em) {
  .title-header {
    font-size: 40px;
  }
}
@media (min-width: 71.875em) {
  .title-header {
    margin: 0 5rem;
    font-size: 60px;
  }
}

.col {
  display: flex;
  flex-direction: column;
}

.container-grid {
  width: 100%;
  position: relative;
}
.container-grid .container-grid-inner {
  width: 100%;
  max-width: 100vw;
  min-height: 450px;
  height: 70vh;
  margin-top: 0.5rem;
  padding-bottom: 5vh;
  box-sizing: content-box;
  overflow-y: scroll;
  scroll-behavior: smooth;
  scrollbar-width: none;
}
@media (min-width: 48em) {
  .container-grid .container-grid-inner {
    display: grid !important;
    grid-template-columns: 1fr 1fr !important;
  }
}
@media (min-width: 71.875em) {
  .container-grid .container-grid-inner {
    max-width: 1200px !important;
    margin: auto !important;
    grid-template-columns: 1fr 1fr 1fr !important;
  }
}
.container-grid .container-grid-inner.beneficio {
  min-height: 200px;
  height: 50vh;
  max-height: 350px;
  margin: 2rem auto;
  display: flex;
  flex-flow: column;
}
@media (min-width: 48em) {
  .container-grid .container-grid-inner.beneficio {
    height: 500px;
    max-height: 500px !important;
    margin: 5rem auto !important;
    padding-bottom: 0;
  }
}
@media (min-width: 71.875em) {
  .container-grid .container-grid-inner.beneficio {
    margin: 5rem auto !important;
  }
}

.slide-container {
  position: relative;
}
@media (min-width: 48em) {
  .slide-container {
    padding-bottom: 3rem;
  }
}
.slide-container .slide-container-inner {
  display: flex;
  flex-direction: row;
  align-items: center;
  box-sizing: content-box;
  overflow-y: visible;
  overflow-x: scroll;
  scroll-behavior: smooth;
  scrollbar-width: none;
}
@media (min-width: 71.875em) {
  .slide-container .slide-container-inner {
    margin-left: 2rem;
  }
}
.slide-container .slide-container-inner::-webkit-scrollbar {
  width: 0 !important;
}

.form-container {
  width: 100%;
  margin: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.btn {
  display: block;
  cursor: pointer;
}

.btn-primary, .btn-mood {
  width: 300px;
  height: 48px;
  margin: 1rem auto;
  background-color: #5ccfd3;
  border: none;
  border-radius: 0.5rem;
  transition: 150ms all ease-in-out !important;
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
}
.btn-primary a, .btn-mood a {
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
}
.btn-primary:hover, .btn-mood:hover {
  background-color: #42bec2;
}

.btn-nav {
  background-color: transparent;
  border: none;
  transition: 150ms all ease-in-out !important;
}
.btn-nav:hover svg {
  width: 100%;
  height: 100%;
}
.btn-nav:hover svg g line,
.btn-nav:hover svg g path {
  stroke: #5ccfd3;
  fill: #5ccfd3;
}

.btn-slide-container {
  width: 48px;
  height: 96px;
  position: absolute;
  top: 50%;
  background-color: #ffffff;
  color: #5ccfd3;
  font-size: 12px;
  font-weight: 700;
  border: none;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
@media (min-width: 48em) {
  .btn-slide-container {
    height: 48px;
    position: relative;
    background-color: #5ccfd3;
    border-radius: 0.5rem !important;
    box-shadow: 0px 0px 4px 0px #5ccfd3;
    color: #ffffff;
    transition: 150ms all ease-in-out !important;
  }
  .btn-slide-container:hover {
    background-color: #ffffff;
    color: #5ccfd3;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
  }
}
.btn-slide-container.left {
  left: 0;
  border-radius: 0 0.5rem 0.5rem 0;
}
.btn-slide-container.right {
  right: 0;
  border-radius: 0.5rem 0 0 0.5rem;
}

.btn-share {
  width: 296px;
  height: 48px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: center;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 14px;
  color: #ffffff;
  border: none;
  border-radius: 0.5rem;
  transition: 150ms all ease-in-out !important;
}
.btn-share:hover {
  transform: scale(0.95);
}
.btn-share.twitter {
  background-color: #0da1f2;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}
.btn-share.facebook {
  background-color: #4267b2;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
}

.beneficio-buttons-wrapper, .mood-buttons-wrapper {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 48em) {
  .beneficio-buttons-wrapper, .mood-buttons-wrapper {
    flex-flow: row-reverse;
    align-items: center;
    gap: 1rem;
    position: absolute;
    top: 1.5rem;
    right: 3rem;
  }
}

.btn-beneficio {
  width: 328px;
  height: 48px;
  margin: 2rem auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: center;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #5ccfd3;
  background-color: white;
  border: none;
  border-radius: 0.5rem;
  transition: 150ms all ease-in-out !important;
}
.btn-beneficio a {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #5ccfd3;
}
.btn-beneficio:hover {
  background-color: #42bec2;
  color: #ffffff;
  border: 1px solid #42bec2;
}
.btn-beneficio:hover a {
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #ffffff;
}
@media (min-width: 48em) {
  .btn-beneficio {
    margin: 0;
    background-color: #5ccfd3;
    border-radius: 0.5rem;
    color: #ffffff;
  }
  .btn-beneficio a {
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: 500;
    color: #ffffff;
  }
  .btn-beneficio:hover:hover {
    background-color: #ffffff;
    color: #5ccfd3;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
  }
  .btn-beneficio:hover:hover a {
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: 500;
    color: #5ccfd3;
  }
}

.btn-beneficio-scroll {
  width: 48px;
  height: 48px;
  background-color: transparent;
  border: none;
  transition: 150ms all ease-in-out !important;
}
@media (min-width: 48em) {
  .btn-beneficio-scroll {
    background-color: #5ccfd3;
    border-radius: 0.5rem;
    color: white;
  }
  .btn-beneficio-scroll:hover {
    background-color: #ffffff;
    color: #5ccfd3;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
  }
}

@media (min-width: 48em) {
  .btn-mood {
    margin: 0;
  }
  .btn-mood:hover {
    background-color: #ffffff;
    color: #5ccfd3;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.5);
  }
  .btn-mood:hover a {
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: 500;
    color: #5ccfd3;
  }
}

